import SpriteSheet from "./SpriteSheet";
import {loadImage} from "./loaders";


export function loadBackgroundSprites() {
    return loadImage('http://www.mariouniverse.com/wp-content/img/sprites/nes/smb/tiles-3.png')
        .then(image => {
            const sprites = new SpriteSheet(image, 16, 16);
            sprites.defineTile('ground', 0, 0);
            sprites.defineTile('sky', 3, 23);
            return sprites;
        });
}

export function loadCharacterSprites() {
    return loadImage('http://www.mariouniverse.com/wp-content/img/sprites/nes/smb/characters.gif')
        .then(image => {
            const sprites = new SpriteSheet(image, 16, 16);
            sprites.define('idle', 276, 44, 16, 16);
            return sprites;
        });
}
